import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Rating from '@material-ui/lab/Rating';
import { IoLogoJavascript } from "react-icons/io";
import { FaReact } from "react-icons/fa";
import { SiRedux } from "react-icons/si";
import { SiMaterialUi } from "react-icons/si";
import { FaBootstrap } from "react-icons/fa";
import { FaSync } from "react-icons/fa";
import { AiFillApi } from "react-icons/ai";
import { FaNode } from "react-icons/fa";
import { AiFillHtml5 } from "react-icons/ai";
import { FaCss3Alt } from "react-icons/fa";
import { FaSass } from "react-icons/fa";
import { FaGithub } from "react-icons/fa";
import { GrLanguage } from "react-icons/gr";


import './Skills.scss'
import Fade from 'react-reveal/Fade';
const useStyles = makeStyles((theme) => ({

    skillcontainer: {
        margin: "0px",
        background: 'black',
        paddingBottom: 40,

    },
    skillitem: {
        flexGrow: 1,
        margin: 8,


    },
    skillrating: {
        // float: "right",
        marginTop: 3,
        // color: 'golden',
        [theme.breakpoints.down('sm')]: {
            margin: 0,
        },
    },
    paper: {
        display: "flex",
        justifyContent: "space-evenly",
        paddingTop: theme.spacing(1),
        textAlign: 'center',
        background: '#1b1b1b',
        color: 'aqua',
        marginBottom: 30,
        marginLeft: 40,
        marginRight: 40,
        "&:hover": {
            //border: '2px solid rgb(5, 248, 37)',
            boxShadow: '1px 4px 9px 6px #2CFC02 ',

            transition: '.3s',
        },
        list: {
            paddingBottom: 10
        }

    }
}));

export default function Skills() {
    const classes = useStyles();

    const skillslist = [{ icon: <IoLogoJavascript color="yellow" />, title: "JavaScript", rating: 4.5 },
    { icon: < FaReact color="#11D4DF " />, title: "React.js", rating: 4.5 }, { icon: < SiRedux color="#990EE4" />, title: "Redux", rating: 3.5 },
    { icon: < SiMaterialUi color="#0E4CE4 " />, title: "Material UI", rating: 4.5 }, { icon: < FaBootstrap color="#0E4CE4 " />, title: "BootStrap", rating: 4 },
    { icon: < FaSync color="#0EE487 " />, title: "Ajax", rating: 4 }, { icon: < AiFillApi color="#0ED0E4 " />, title: "Api Handling", rating: 4 },
    { icon: < FaNode color="#358015 " />, title: "Node.js", rating: 2.5 }, { icon: < AiFillHtml5 color="#F0220B " />, title: "Html & CSS", rating: 4 },
    { icon: < FaSass color="#990EE4" />, title: "SASS", rating: 4 }, { icon: < FaGithub color="#F6F7FA " />, title: "Git & GitHub", rating: 3.5 },
    { icon: < GrLanguage color="#0529E7 " />, title: "C & C++", rating: 4 }]

    let skill = skillslist.map((skills) => (

        <Grid item xs={12} sm={6} className="skill-paper">
            <Paper className={classes.paper}><span className="skills-icon" >{skills.icon}</span>{skills.title}<Rating className={classes.skillrating} name="half-rating-read" defaultValue={skills.rating} precision={0.5} readOnly /></Paper>
        </Grid>

    ))

    return (
        <div className={classes.skillcontainer} id="skills">
            <h1 className="featured-head" >Skills</h1>
            <div className={classes.skillitem}>
                <Fade left>
                    <Grid container  >
                        {skill}
                    </Grid>
                </Fade>
            </div>

        </div>
    )
}


